body {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  line-height: 24px;
}

header nav {
  background-color: transparent;
}
header nav .site_logo {
  max-width: 250px;
}
header nav .menu-item {
  padding: 0 15px;
}
header nav .menu-item .nav-link {
  color: white;
}
header .topbar {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #ce3095;
  padding: 10px 0;
}
header .topbar p {
  margin: 0;
}
header .topbar .button {
  background-color: #0db4b9;
  color: white;
  border-radius: 17.5px;
  padding: 5px 30px;
  transition: all 0.3s;
  display: inline-block;
  margin-left: 15px;
}
header .topbar .button:hover {
  cursor: pointer;
  text-decoration: none;
  filter: brightness(95%);
  transform: scale(0.95);
}
header .overlay {
  position: absolute;
  width: 100%;
  height: 700px;
  background-image: linear-gradient(to right, #0db4b9, #1d1045);
  top: 0;
  left: 0;
  z-index: -1;
  clip-path: polygon(0% 100%, 100% 44%, 100% 0%, 0% 0%);
}/*# sourceMappingURL=data:application/json;charset=utf-8;base64, */